import React, { useState, useEffect } from "react";

import {
  Panel,
  View,
  Root,
  PanelHeader,
  CellButton
} from "@vkontakte/vkui";

import bridge from "@vkontakte/vk-bridge";


function App() {
  const [ activePanel, setActivePanel ] = useState("general-top");
  const [ token, setToken ] = useState(null);
  
  async function getGeneralTop() {
	console.log(await fetch("https://KitKit.pythonanywhere.com/", {
	  method: "post",
	  body: {
	    query: "get_general_top"
	  }
	}));
  }
  
  async function addGroup() {
	const groupId = await bridge.send("VKWebAppAddToCommunity");
	await fetch("https://KitKit.pythonanywhere.com/", {
	  method: "post",
	  body: {
		query: "add_group",
		vk_group_id: groupId.group_id
	  },
	  headers: {
		"Content-Type": "application/json"
	  }
	});
  }
  
  return (
    <Root activeView="view">
	  <View activePanel="top" id="view">
	    <Panel id="top">
		  <PanelHeader>Маня автоматизатор</PanelHeader>
		  <CellButton centered={ true } onClick={ addGroup }>Добавить сообщество</CellButton>
		</Panel>
	  </View>
	</Root>
  );
}

export default App;
